.tags__tag-box {
  overflow: hidden;
  height: auto;
  margin: 20px 0;
  max-height: 45px; 
  -webkit-transition: max-height 0.7s ease; 
  -o-transition: max-height 0.7s ease; 
  transition: max-height 0.7s ease;
}

.tags__tag-box:hover {
  max-height: 600px; 
}

.tags__item {
  margin: 5px;
}

.tags__title {
  border: 2px solid gainsboro;
  border-radius: 5px;
  padding: 0 5px;
  margin-right: 2px;
  font-size: 14px;
  margin-right: 5px;
}

.tags__badge {
  font-size: 11px;
  padding: 5px;
  text-align: center;
  background-color: #bfbfbf;
}


