.topics__back {
  color: gray;
  padding: 20px 0;
  text-decoration: none;
  text-transform: uppercase;
}

.topic__body {
  margin: 20px 0;
}

.topics__back:hover {
  -webkit-box-shadow: 5px black;
          box-shadow: 5px black;
  color: black;
}

.topic__container {
  overflow: auto;
}

.topics__content-item {
  margin: 10px;
  margin-bottom: 70px;
}


.topic__type {
  margin: 0 10px;
  border-radius: 5px;
  color: white;
  padding: 3px;
  font-size: 14px;
}

.topic__bottom {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-bottom: 20px;
}

.topic__edit {
  float: right;
  margin-top: 20px;
}

.topic__impact {
  font-size: 16px;
  
}

.topic__match {
  color: #000;
  font-size: 13px;
  margin: 0 auto;
  display: inline-block;  
  min-width: 25px;
  text-align: center;
  margin: 0 5px;
  padding: 0 5px;

  border: 2px solid tomato;
  border-radius: 2px;
}

.comment__owner {
  text-align: right;
}

.comment__section {
  /* border-top: 2px solid lightblue; */
  padding: 10px;
  margin: 30px 0;
}

.comment__placeholder {
  margin: 20px 0;
  padding: 0 5px;
  padding-top: 9px;
  padding-bottom: 1px;
  background-color: #f1f1f1;
  border-radius: 5px;
}


